import React, { useContext } from 'react';

import styled from 'styled-components';

import { colToLeft, rowToTop } from '../shared/card-functions';

import GameStateContext from '../contexts/GameStateContext';

const left = colToLeft(12, 6) - 14;
const top = rowToTop(6);

const divstyle = {
  position: 'absolute',
  left: `${left}px`,
  top: `${top}px`,
  width: '40px',
  height: '40px',
};

const Button = styled.button`
  background: #761d38;
  color: white;
  font-size: 1em;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid #761d38;
  border-radius: 3px;
`;

const DealButton = () => {
  const { dealCards, gamePlaying } = useContext(GameStateContext);

  const dealCardAndAction = () => {
    dealCards();
  };

  // only show the button if we are not playing the game yet
  if (gamePlaying) {
    return null;
  }

  return (
    <div style={divstyle}>
      <Button onClick={dealCardAndAction}>Deal</Button>
    </div>
  );
};

export default DealButton;
